const getDate = (numberOfDay = 0) => {
	let number;
	
	if (isNaN(Number(numberOfDay))) {
		number = 0;
	}
	
	switch (true) {
		case isNaN(Number(numberOfDay)):
			number = 0;
			break;
		
		case numberOfDay < 0:
			number = 0;
			break;
			
		default:
			number = Math.abs(Math.floor(numberOfDay))
	}
	
	const today = new Date();
	const nextDay = new Date(today);
	const date = nextDay.setDate(nextDay.getDate() + number);
	const newDate = new Date(date);
	
	return newDate.toLocaleDateString('RU', {
		year: '2-digit',
		month: '2-digit',
		day: '2-digit',
	});
};

export default getDate;
